.navbar {
  position: fixed;
  top: 0;
  z-index: 1;
  height: 50px;
  width: 100%;
  background-color: rgba(160,140,252,.5);
  display: flex;
  justify-content: center;
  
 


}
  
  .navbar a {
    color: #500303;
    text-decoration: none;
    font-weight: 600;
    padding: 5px 10px;
    
    
  }
  
  .navbar a.active {
    color: #f5f5f5;
    background-color: #555;
    border-radius: 5px;
    
  }
  

.navbar-btn {
  border: none;
  color: rgb(255, 255, 255);
  display: inline;
  font-size: 16px;
  padding: 0 20px;

  background-color: transparent;
  
  

  
}

.navbar-btn:hover {
 
  
  color: rgb(24, 13, 73,.5);
  
}